.container {
    width: 100%;
}

/* Header */
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: var(--space-2);
}

.label {
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--text-secondary);
}

.value {
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-muted);
    font-variant-numeric: tabular-nums;
}

/* Track */
.track {
    position: relative;
    width: 100%;
    background: var(--bg-dark-tertiary);
    border-radius: var(--radius-full);
    overflow: hidden;
}

/* Sizes */
.sm .track {
    height: 6px;
}

.md .track {
    height: 10px;
}

.lg .track {
    height: 14px;
}

/* Fill */
.fill {
    height: 100%;
    border-radius: var(--radius-full);
    position: relative;
}

.fill-default {
    background: var(--primary-600);
}

.fill-xp {
    background: var(--gradient-xp);
}

.fill-streak {
    background: linear-gradient(90deg, #f59e0b, #ef4444);
}

.fill-goal {
    background: linear-gradient(90deg, #10b981, #06b6d4);
}

/* Shimmer */
.shimmer {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: linear-gradient(90deg,
            transparent 0%,
            rgba(255, 255, 255, 0.3) 50%,
            transparent 100%);
    background-size: 200% 100%;
    animation: shimmer 2s infinite;
    border-radius: var(--radius-full);
}

@keyframes shimmer {
    0% {
        background-position: -200% 0;
    }

    100% {
        background-position: 200% 0;
    }
}

/* Complete Icon */
.completeIcon {
    position: absolute;
    right: 4px;
    top: 50%;
    transform: translateY(-50%);
    width: 18px;
    height: 18px;
    background: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    font-weight: bold;
    color: #10b981;
}

/* XP Bar */
.xpBar {
    display: flex;
    align-items: center;
    gap: var(--space-3);
    padding: var(--space-2) var(--space-3);
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    border-radius: var(--radius-full);
}

.xpBarCompact {
    padding: var(--space-1) var(--space-2);
    gap: var(--space-2);
}

.xpLevel {
    width: 28px;
    height: 28px;
    background: linear-gradient(135deg, var(--primary-600), var(--primary-700));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.xpBarCompact .xpLevel {
    width: 22px;
    height: 22px;
}

.xpLevelNumber {
    font-size: 0.75rem;
    font-weight: 700;
    color: white;
}

.xpBarCompact .xpLevelNumber {
    font-size: 0.625rem;
}

.xpTrack {
    flex: 1;
    height: 8px;
    background: var(--bg-dark-tertiary);
    border-radius: var(--radius-full);
    position: relative;
    overflow: hidden;
    min-width: 80px;
}

.xpBarCompact .xpTrack {
    height: 6px;
    min-width: 60px;
}

.xpFill {
    height: 100%;
    background: var(--gradient-xp);
    border-radius: var(--radius-full);
}

.xpShimmer {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background: linear-gradient(90deg,
            transparent 0%,
            rgba(255, 255, 255, 0.2) 50%,
            transparent 100%);
    background-size: 200% 100%;
    animation: shimmer 2.5s infinite;
    border-radius: var(--radius-full);
}

.xpText {
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--text-muted);
    font-variant-numeric: tabular-nums;
    white-space: nowrap;
}

/* XP Gain Animation */
.xpGain {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2rem;
    font-weight: 800;
    font-family: var(--font-display);
    background: var(--gradient-text);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 40px rgba(139, 92, 246, 0.5);
    z-index: var(--z-toast);
    pointer-events: none;
}